.detail__menu {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 80px;
  padding-right: 80px;
}

.detail__menu a {
  color: white;
}

.detail__close {
  display: none;
}

.detail__open {
  display: flex;
  justify-content: center;
  position: absolute;
  height: 242px;
  border-bottom: 0.5px solid #e5e5e5;
  background: -webkit-radial-gradient(
    center,
    ellipse cover,
    rgba(255, 99, 95, 1) 0%,
    rgba(191, 72, 72, 1) 100%
  );
  width: 100%;
  color: white;
  animation: detail__anim 1s;
  z-index: 1;
}

@keyframes detail__anim {
  0% {
    margin-top: -72px;
  }

  100% {
    margin-top: 0px;
  }
}

.menu__box {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  color: white;
  height: 160px;
  margin-top: 75px;
  margin-bottom: 20px;
}

.menu__box a {
  color: white;
}

.menu__box a:hover {
  color: white;
}
