.carusel__box {
  position: absolute;
  height: 530px;
  width: 100%;
}

.carusel__contents {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: rgba(0, 0, 0, 0.5);
  background: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 0.5) 0%,
    rgba(0, 0, 0, 0.3) 100%
  );
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#000000', endColorstr='#000000', GradientType=0 );
  height: inherit;
  color: white;
}

.carusel__contents h1 {
  font-size: 35px;
  padding-bottom: 40px;
}

.showing__left {
  z-index: 1;
  animation: showingToLeft 2s;
}

.showing__right {
  z-index: 1;
  animation: showingToRight 2s;
}

.closing__left {
  animation: closingToLeft 2s;
}

.closing__right {
  animation: closingToRight 2s;
}

@keyframes showingToLeft {
  from {
    right: -100%;
  }
  to {
    right: 0;
  }
}

@keyframes closingToLeft {
  from {
    left: 0;
  }
  to {
    left: -100%;
  }
}

@keyframes showingToRight {
  from {
    left: -100%;
  }
  to {
    left: 0;
  }
}

@keyframes closingToRight {
  from {
    right: 0;
  }
  to {
    right: -100%;
  }
}
